<template>
  <div class="propertyPayPopup">
    <van-popup
      v-model="showPop"
      position="bottom"
      class="vanPopup"
      @click="closePopup"
      @click-overlay="closePopup"
    >
      <div class="payModeBox">
        <div class="payModeTitle">支付方式</div>
        <div v-show="isAlipay" class="payModeItem" @click="alipay(1)">
          <img src="@/assets/images/common/icon_alipay.png" alt />
          <div>支付宝</div>
        </div>
<!--        <div v-show="isWechart" class="payModeItem" @click="alipay(0)">-->
<!--          <img src="@/assets/images/common/icon_wechart.png" alt />-->
<!--          <div>微信</div>-->
<!--        </div>-->
      </div>
      <div class="cancelBtn" @click="closePopup">取消</div>
    </van-popup>
  </div>
</template>

<script>
import { getPrepayOrderDetailService } from "../../../services/propertyBillService";
import { Dialog } from "vant";
export default {
  name: "propertyPayPopup",
  props: {
    isAlipay: { type: Boolean, default: true },
    isWechart: { type: Boolean, default: true },
    payParam: { type: Object }
  },
  data() {
    return {
      showPop: true //popup的显示，在组件中，默认是显示，不用父组件传值
    };
  },
  mounted() {},
  methods: {
    closePopup() {
      this.$emit("closePopup");
    },
    checkOrderStatus() {
      this.$emit("checkOrderStatus");
    },
    /*银联下单支付*/
    alipay(index) {
      //x订单编码//y支付金额,index 0 微信 1 支付宝 p 支付参数
      console.log(JSON.stringify(this.payParam));
      this.payParam.payWay = index;
      //ios测试打开
      window.broadcaster.fireNativeEvent(
        "ionicToNative",
        {
          DATA: this.payParam,
          FLAG: "234"
        },
        () => {
          //关闭弹窗
          this.closePopup();
        }
      );
    }
  }
};
</script>

<style scoped lang="scss">
/* 支付弹窗 */

.vanPopup {
  max-height: 257px;
  background: transparent;
  padding: 0 8px 8px;
  /* padding-bottom: 8px; */
  box-sizing: border-box;

  .payModeBox {
    width: 359px;
    max-height: 181px;
    background: #fff;
    margin: 0 auto;
    border-radius: 4px;
    margin-bottom: 8px;

    .payModeTitle {
      padding: 0;
      text-align: center;
      height: 61px;
      line-height: 61px;
      border-bottom: 1px solid #ececec;
      font-family: PingFangSC-Medium;
      font-size: 16px;
      color: #3a3a3a;
      letter-spacing: 0.06px;
    }

    .payModeItem {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 60px;
      border-color: #ececec;
      border-bottom: 1px solid #e5e5e5;

      img {
        width: 26px;
        height: 26px;
      }

      div {
        font-family: PingFangSC-Regular;
        font-size: 16px;
        color: #3a3a3a;
        letter-spacing: 0.06px;
        margin-left: 6px;
      }
    }

    .payModeItem:last-child {
      border-bottom: 0px;
    }
  }

  .cancelBtn {
    width: 359px;
    height: 60px;
    line-height: 60px;
    text-align: center;
    border-radius: 4px;
    background: #fff;
    margin: 0 auto;
    font-family: PingFangSC-Regular;
    font-size: 16px;
    color: #3a3a3a;
    letter-spacing: 0.06px;
  }
}
</style>
