<template>
  <div class="propertyOrderGotoPay" :class="{themeL:bindThemeL}">
    <div class="orderPayBottom">
        <div class="amount">
            <span>总计：</span>
            <span style="font-size: 16px;" :style="{fontSize:amountFont}">￥{{amount| keepTwoNum}}</span>
        </div>
        <div class="payBtn" @click="goToPay()">立即支付</div>
    </div>
  </div>
</template>

<script>
export default {
  name: "propertyOrderGotoPay",
  props: ["amount"],
  data() {
    return {
      amount: this.amount,
      /*大字模式*/
      bindThemeL: this.$curTheme==0?false:true,
      amountFont: this.$curTheme==0?"16px":"20px",
    };
  },
  methods: {
    goToPay(){
      this.$emit("goToPay");
    }
  }
};
</script>

<style scoped>
.propertyOrderGotoPay .orderPayBottom{
    position:fixed;
    bottom: 0px;
    height: 50px;
    line-height: 50px;
    width: 100%;
    display: flex;
    box-shadow: 0px -1px 2px #e2e2e2;
}

.propertyOrderGotoPay .orderPayBottom .amount
{
    height:100%;
    width: 50%;
    background-color: white;
    font-size: 12px;
    text-align: center;
}
.themeL .orderPayBottom .amount
{
  font-size: 15px;
}
.propertyOrderGotoPay .orderPayBottom .payBtn
{
    height:100%;
    width: 50%;
    background-color: #17B7BD;
    font-size: 16px;
    text-align: center;
    color: white;
}
.themeL .orderPayBottom .payBtn
{
  font-size: 18px;
}
</style>
