<template>
  <div class="propertyPreOrderPay" :class="{themeL:bindThemeL}">
    <!-- 头部 -->
    <navigation-bar  @onLeftClick="onClickLeft" :pageName="title" :bothWidth="headerItemWidth" >
    </navigation-bar>
    <div class="propertyPreOrderPayContent">
      <div style="background-color: #f2f2f2;"></div>
      <div class="list">
        <div class="item" style="border:none;">
          <div style="display:flex">
            <van-image width="54" height="54" :src="houseImg" />
            <div style="left:16px;flex: 1">
              <div
                style="min-height:27px;line-height: 27px;font-size: 16px;color: black;"
                :style="{fontSize:roomNameFont}"
              >{{orderDetail.roomName}}</div>
              <p
                style="height:27px;line-height: 27px;font-size: 12px;color: #9a9a9a;"
                :style="{fontSize:customerNameFont}"
              >房主：{{orderDetail.customerName}}</p>
            </div>
            <div></div>
          </div>
        </div>
        <div class="item">
          预缴周期
          <span class="item-note" style="color:black">
            <span>{{months}}</span>个月
          </span>
        </div>
        <div class="item" style="padding-bottom:0px;border-bottom: none;">
          <div style="color: #9a9a9a;">固定费用项</div>
        </div>

        <div class="item" style="padding-top:0px;border: none">
          <div
            v-for="(item,cycleIndex) in cycleOptionList"
            :key="cycleIndex"
            style="vertical-align: center;margin-top: 16px;height: 20px;display: flex;"
          >
            <div style="display: flex;">
              <div style="height: 20px;">{{item.payOptionName}}</div>
              <div v-if="item.discount<100" class="saleTag">{{item.discount/10}}折</div>
            </div>

            <div
              class="item-note"
              style="height: 20px;display: flex;justify-content: flex-end;flex-grow:1;"
            >
              <div
                class="fs12"
                v-if="item.discount<100"
                style="height: 20px;margin-right:4px;text-decoration:line-through;"
              >￥{{item.advanceMoney}}</div>
              <div
                class="fs16"
                v-if="orderDetail.useDiscount==1"
                style="height: 20px;color:black"
              >￥{{item.chargeMoney}}</div>
              <div
                class="fs16"
                v-if="orderDetail.useDiscount==0"
                style="height: 20px;color:black"
              >￥{{item.advanceMoney}}</div>
            </div>
          </div>
        </div>

        <div class="item" v-if="noCycleOptionList.length>0">
          <div style="color: #9a9a9a;">非固定费用项</div>
        </div>

        <div class="item" style="padding-top:0px;border: none;" v-if="noCycleOptionList.length>0">
          <div class="pt16"  v-for="(item,nocycleIndex) in noCycleOptionList"
            :key="nocycleIndex">
            <span style="height: 22px;">{{item.payOptionName}}</span>
            <span class="item-note" style="color:black;">￥{{item.chargeMoney}}</span>
          </div>
        </div>
        <div class="item">
          <div style="display: flex;">
            <div style="width: 50%;">
              <div>
                <p style="color:black;font-size:16px;" :style="{fontSize:pointFont}">积分抵扣</p>
                <p style="color:#9a9a9a;font-size:12px;">100积分可抵1元</p>
              </div>
            </div>
            <div style="width: 50%;" align="right">
              <div
                class="change_point"
                style="border: 1px solid; border-radius: 3px; border-color: #bdbdbd;margin-top: 6px;"
              >
                <van-image class="delete_icon" :src="cutImg" @click="pointReduce()" />
                <input type="number" class="point_num" v-model="pointVO.deductionPoints" />
                <van-image class="add_icon" :src="addImg" @click="pointAdd()" />
              </div>
            </div>
          </div>
        </div>

        <div class="item" style="display:flex;">
          <div @click="integralInstructionFn()">
            <div style="display:flex;">
              <p style="color:black;font-size:16px;height: 20px;" :style="{fontSize:pointFont}">使用全部积分抵扣</p>
              <p
                class="pointRule"
              >?</p>
            </div>
            <p style="color:#9a9a9a;font-size:12px;height: 20px;">本次可用最高积分为{{pointAviable}}</p>
          </div>
          <div class="item-note" style="height:31px;right: 0px;flex-grow:1;">
            <img
              class="choose_is_point"
              :src="isAllPoints?switchOnImg:switchOffImg"
              @click="useAllPoints()"
            />
          </div>
        </div>
        <div class="item" style="padding-bottom:0px;font-size:12px;color:#3a3a3a" :style="{fontSize:pointFont}">
          <span class="btTitle">订单总额</span>
          <span class="item-note" style="color:black">￥{{totalAmount| keepTwoNum}}</span>
        </div>
        <div
          class="item"
          style="border: none;padding-bottom:0px;padding-top:8px;font-size:12px;color:#3a3a3a"
          :style="{fontSize:pointFont}"
        >
          <span class="btTitle">积分抵扣</span>
          <span class="item-note" style="color:#17B7BD">-￥{{pointVO.deductionMoney| keepTwoNum}}</span>
        </div>
        <div
          class="item"
          v-if="discountMoney>0"
          style="border-top:none;padding-top:8px;font-size:12px;color:#3a3a3a"
          :style="{fontSize:pointFont}"
        >
          <span class="btTitle">折扣优惠</span>
          <span class="item-note" style="color:#17B7BD">-￥{{discountMoney| keepTwoNum}}</span>
        </div>
      </div>
    </div>

    <div style="height:87px;"></div>
    <property-order-goto-pay :amount="unionPayMoney" @goToPay="goToPay"></property-order-goto-pay>
    <property-pay-popup
      v-if="payWindowShow"
      :payParam="payParam"
      :isAlipay="isAlipay"
      :isWechart="isWechart"
      @closePopup="closePopup"
      @checkOrderStatus="checkOrderStatus"
    ></property-pay-popup>
  </div>
</template>

<script>
import {
  getPrepayOrderDetailService,
  prepareToPayPreOrderService,
  getUseLimitService
} from "../../../services/propertyBillService";
import propertyOrderGotoPay from "./propertyOrderGotoPay";
import propertyPayPopup from "./propertyPayPopup";
import NavigationBar from '@/common/currency/NavigationBar.vue';
import Vue from "vue";
import { Dialog } from "vant";
Vue.use(Dialog);
export default {
  name: "propertyPreOrderPay",
  data() {
    return {
      billNo: "",
      orderDetail: {}, //订单详情
      isAllPoints: 0, //使用全部积分标识
      pointAviable: 0, //可用积分
      totalAmount: 0.0,
      unionPayMoney: 0.0,
      discountMoney: 0.0,
      pointVO: { deductionPoints: 0, deductionMoney: 0 },
      months: 0,
      cycleOptionList: [],
      noCycleOptionList: [],
      payWindowShow: false, //支付弹窗
      isAlipay: false,
      isWechart: false,
      needRefresh: true,
      headerItemWidth:{
          width:'84px'
      },
      title:'订单支付',
      switchOnImg: require("../../../assets/images/home/property/payment/switch_on.png"),
      switchOffImg: require("../../../assets/images/home/property/payment/switch_off.png"),
      addImg: require("../../../assets/images/home/property/advancePayment/bg_add.png"),
      cutImg: require("../../../assets/images/home/property/advancePayment/bg_cut.png"),
      houseImg: require("../../../assets/images/home/property/payment/house_house.png"),
      /*大字模式*/
      bindThemeL: this.$curTheme==0?false:true,
      customerNameFont: this.$curTheme==0?"12px":"15px",
      roomNameFont: this.$curTheme==0?"16px":"18px",
      pointFont: this.$curTheme==0?"16px":"17px",
    };
  },
  components: {
    NavigationBar,
    "property-order-goto-pay": propertyOrderGotoPay,
    "property-pay-popup": propertyPayPopup
  },
  /* 跳转积分时缓存页面数据 */
  beforeRouteLeave(to, from, next) {
    if (to.name == "readTextPage") {
      this.needRefresh = false;
    } else {
      this.needRefresh = true;
    }
    next();
  },
  activated() {
    if (this.needRefresh) {
      window.addEventListener("native.keyboardhide", this.pointValid);
      cordova.plugins.Keyboard.hideKeyboardAccessoryBar(false);
      this.cycleOptionList = [];
      this.noCycleOptionList = [];
      this.billNo = this.$route.params.billNo;
      this.pointVO.deductionPoints = 0;
      this.pointVO.deductionMoney = 0;
      this.isAllPoints = 0;
      this.getPreOrderDetail();
    }
  },
  methods: {
    /* 预缴订单详情 */
    getPreOrderDetail() {
      let reqData = { billNo: this.billNo };
      getPrepayOrderDetailService(reqData)
        .then(result => {
          if (result.resultCode == 200) {
            this.orderDetail = result.data;
            //获得每个收费项目
            this.updateOptionList(result.data.detailList);

            this.totalAmount = parseFloat(result.data.payMoneyTotal).toFixed(2);
            this.unionPayMoney = parseFloat(result.data.unionPayMoney).toFixed(
              2
            );
            if (
              result.data.deductionPoints != null &&
              result.data.deductionPoints != ""
            ) {
              this.pointVO.deductionMoney = parseFloat(
                result.data.deductionMoney
              ).toFixed(2);
              this.pointVO.deductionPoints = result.data.deductionPoints;
            } else {
              this.pointVO.deductionPoints = 0;
            }
            this.months = result.data.months;

            // 获取用户积分
            this.getUseOrderLimitFn();
          } else {
            this.$toast(result.msg ? result.msg : "网络异常，请稍后重试！");
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.$toast("网络异常，请稍后重试！");
        });
    },
    /* 组装每个收费项目 */
    updateOptionList(detailList) {
      for (var i = 0; i < detailList.length; i++) {
        if (detailList[i].feeType == 0) {
          this.cycleOptionList.push(detailList[i]);
        } else {
          this.noCycleOptionList.push(detailList[i]);
        }

        //计算物业折扣优惠金额
        var dismoney = this.discountMoney;
        var listmoney = detailList[i].discountMoney;
        this.discountMoney = dismoney + listmoney;
      }
    },
    /* 获取用户可用积分 */
    getUseOrderLimitFn() {
      var reqData = {
        ownerCode: this.$userInfo.OWNER_CODE,
        money: this.totalAmount
      };
      getUseLimitService(reqData)
        .then(result => {
          if (result.code == 200) {
            //true表示返回成功
            this.pointAviable = Math.floor(result.data / 100) * 100;
          } else {
            this.pointAviable = 0;
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.$toast("网络异常，请稍后重试！");
        });
    },
    //积分加
    pointAdd() {
      if (this.pointVO.deductionPoints+100 > this.pointAviable) {
        this.$toast("本单最高抵扣" + this.pointAviable + "积分");
      } else {
        this.pointVO.deductionPoints += 100;
        this.pointVO.deductionMoney += 1;
        if (this.pointVO.deductionPoints == this.pointAviable) {
          this.isAllPoints = 1;
        }
        this.calculateUnionPayMoney();
      }
    },

    //积分减
    pointReduce() {
      this.isAllPoints = 0;
      if (this.pointVO.deductionPoints <= 0) {
        this.$toast("抵扣积分不能为负数");
      } else {
        this.pointVO.deductionPoints -= 100;
        this.pointVO.deductionMoney -= 1;
        this.calculateUnionPayMoney();
      }
    },
    pointValid(){
      let value = this.pointVO.deductionPoints;
      if (isNaN(value)) {
        this.pointVO.deductionPoints=0;
        return;
      }
      if (value > this.pointAviable) {
          this.pointVO.deductionPoints = Math.floor(this.pointAviable / 100) * 100;
          this.$toast('本单最高抵扣' + this.pointAviable + '积分');
      } else if (value < 0) {
          this.pointVO.deductionPoints = 0;
          this.$toast('抵扣积分不能为负数');
      } else {
          var num = value;
          this.pointVO.deductionPoints = Math.floor(value / 100) * 100;
          if (num != this.pointVO.deductionPoints) {
              this.$toast('抵扣积分为整百');
          }
      }
      console.log(this.pointVO.deductionPoints);
      this.pointVO.deductionMoney = this.pointVO.deductionPoints / 100;
      this.calculateUnionPayMoney();
    },
    /* 跳转积分说明 */
    integralInstructionFn() {
      this.$router.push({
        name: "readTextPage",
        params: { pageType: 5 }
      });
    },
    /* 计算订单金额 */
    calculateUnionPayMoney() {
      this.unionPayMoney =
        parseFloat(this.totalAmount).toFixed(2) -
        parseFloat(this.discountMoney).toFixed(2) -
        parseFloat(this.pointVO.deductionMoney).toFixed(2);

      //add for ZHSQYYPT-8133
      this.unionPayMoney = parseFloat(this.unionPayMoney).toFixed(2)
      console.log("【订单总额】this.unionPayMoney = "+this.unionPayMoney)
    },
    /* 使用全部积分 */
    useAllPoints() {
      this.isAllPoints == 1 ? (this.isAllPoints = 0) : (this.isAllPoints = 1);

      if (this.isAllPoints == 1) {
        this.pointVO.deductionPoints = Math.floor(this.pointAviable / 100) * 100;
      } else {
        this.pointVO.deductionPoints = 0;
      }
      this.pointVO.deductionMoney = this.pointVO.deductionPoints / 100;
      this.calculateUnionPayMoney();
    },
    onClickLeft() {
      window.removeEventListener("native.keyboardhide", this.pointValid);
      cordova.plugins.Keyboard.hideKeyboardAccessoryBar(true);
      this.$router.go(-2);
    },
    /* 立即支付 */
    goToPay() {
      if(this.unionPayMoney < 0){
        this.$toast("订单生成错误，金额不能为负数");
        return;
      }
      let reqData = {
        billNo: this.billNo,
        deductionPoints: this.pointVO.deductionPoints,
        deductionMoney: this.pointVO.deductionMoney,
        unionPayMoney: this.unionPayMoney
      };
      prepareToPayPreOrderService(reqData)
        .then(result => {
          if (result.resultCode == 200) {
            /* 获取支付参数 */
            this.payParam = result.data;
            /* 判断支持的支付方式 */
            if (result.data.IS_ALI_PAY == "1") {
              this.isAlipay = true;
            }
            if (result.data.IS_WX_PAY == "1") {
              this.isWechart = true;
            }
            /* 支付弹窗 */
            this.payWindowShow = true;
            // /*监听ios支付结果 检测当前订单状态*/
            window.broadcaster.addEventListener("paymentBack", this.payCallBack);
          } else {
            this.$toast(result.msg ? result.msg : "网络异常，请稍后重试！");
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.$toast("网络异常，请稍后重试！");
        });
    },
    /* 原生回调 */
    payCallBack() {
      this.checkOrderStatus();
    },
    /* 检查订单状态 */
    checkOrderStatus() {
      /* 取消监听，避免重复监听 */
      window.broadcaster.removeEventListener("paymentBack",this.payCallBack);
      /* 获取订单数据 */
      let reqData = { billNo: this.billNo };
      getPrepayOrderDetailService(reqData)
        .then(result => {
          if (result.resultCode == 200) {
            if (result.data.billStatus == 1) {
              //订单状态为已支付，跳转支付成功界面
              this.$router.push({
                name: "paymentPaySuccess",
                params: { orderData: result.data }
              });
            } else {
              //支付失败，返回列表页
              this.$toast({
                duration: 500,
                message: "支付失败",
                onClose: () => {
                  this.$router.go(-2);
                }
              });
            }
          } else {
            this.$toast(result.msg ? result.msg : "网络异常，请稍后重试！");
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.$toast("网络异常，请稍后重试！");
        });
    },
    /* 关闭支付弹窗 */
    closePopup() {
      this.payWindowShow = false;
    }
  }
};
</script>

<style scoped lang="scss">
.propertyPreOrderPay{
  height: 100%;
  display: flex;
  flex-direction: column;
}
.propertyPreOrderPayContent {
  margin: 0 20px 0 20px;
  flex: 1;
  overflow-y: scroll;
}
.propertyPreOrderPayContent::-webkit-scrollbar {
  display: none;
}
.propertyPreOrderPay .list {
  position: relative;
  padding-top: 1px;
  padding-bottom: 1px;
  padding-left: 0;
  margin-bottom: 20px;
}
.propertyPreOrderPay .item {
  border-color: #ddd;
  background-color: #fff;
  color: #444;
  position: relative;
  z-index: 2;
  display: block;
  margin: -1px 0;
  padding: 16px 0;
  border-width: 1px;
  border-style: solid;
  font-size: 12px;
  border-left: none;
  border-right: none;
}
.themeL .item {
  font-size: 17px;
}
.propertyPreOrderPay .item-note {
  float: right;
  font-size: 12px;
  color: #333333;
  text-align: right;
  letter-spacing:0.8px;
  /* margin-right: 16px; */
}
.themeL .item-note {
  font-size: 17px;
}
/* 积分 */
.propertyPreOrderPay .change_point {
  overflow: hidden;
  width: 104px;
  float: right;
  height: 28px;
  background: #ffffff;
  display: flex;
  position: relative;
}
.propertyPreOrderPay .add_icon {
  float: right;
  /* height: 28px;
    line-height: 28px;
    width: 28px;
    right: 16px; */
  color: #9a9a9a;
  font-size: 22px;
  text-align: center;
  padding: 6px;
}

.propertyPreOrderPay .delete_icon {
  float: left;
  /* height: 28px;
    line-height: 28px;
    width: 28px; */
  color: #9a9a9a;
  font-size: 22px;
  text-align: center;
  padding: 6px;
}

.propertyPreOrderPay .point_num {
  width: 48px;
  font-family: PingFangSC-Regular;
  font-size: 14px;
  color: black;
  background-color: white;
  text-align: center;
  border-radius: 2px;
  height: 28px;
  line-height: 28px;
  padding: 0px;
  border: none;
}
.pointRule{
  text-align: center;
  font-size:10px;
  background-color:#bdbdbd;
  color:white;
  border-radius:7px;
  height: 14px;
  line-height: 15px; 
  width: 14px;
  margin-right: 10px;
  margin-top: 2px;
}

.propertyPreOrderPay .choose_is_point {
  float: right;
  width: 59px;
  height: 32px;
}
p {
  margin: 0;
}

/* 支付弹窗 */

.vanPopup {
  max-height: 257px;
  background: transparent;
  padding: 0 8px 8px;
  /* padding-bottom: 8px; */
  box-sizing: border-box;

  .payModeBox {
    width: 359px;
    max-height: 181px;
    background: #fff;
    margin: 0 auto;
    border-radius: 4px;
    margin-bottom: 8px;

    .payModeTitle {
      padding: 0;
      text-align: center;
      height: 61px;
      line-height: 61px;
      border-bottom: 1px solid #ececec;
      font-family: PingFangSC-Medium;
      font-size: 16px;
      color: #3a3a3a;
      letter-spacing: 0.06px;
    }

    .payModeItem {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 60px;
      border-color: #ececec;
      border-bottom: 1px solid #e5e5e5;

      img {
        width: 26px;
        height: 26px;
      }

      div {
        font-family: PingFangSC-Regular;
        font-size: 16px;
        color: #3a3a3a;
        letter-spacing: 0.06px;
        margin-left: 6px;
      }
    }

    .payModeItem:last-child {
      border-bottom: 0px;
    }
  }

  .cancelBtn {
    width: 359px;
    height: 60px;
    line-height: 60px;
    text-align: center;
    border-radius: 4px;
    background: #fff;
    margin: 0 auto;
    font-family: PingFangSC-Regular;
    font-size: 16px;
    color: #3a3a3a;
    letter-spacing: 0.06px;
  }
  /* 底部样式 */
  .btTitle{
    font-size:12px;
    color:#3a3a3a
  }
}
</style>
